.choice-container {
    text-align: left;
    border: 1px solid #ccc;
    position: relative;
    border-radius: 6px;
    cursor: pointer;
    width: max-content;
    display: inline-block;
}

.dropdown-item {
        padding: 7px 10px;
        cursor: pointer;
        transition: all 0.35s ease;
        border-radius: 6px;
        font-weight: 500;
        font-family: NotoSerifJP;
        display: flex; 
        flex-direction:column;
    }

    .dropdown-input {
        padding: 7px 11px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        user-select: none;
        gap: 7px;
    }
    
    .dropdown-menu {
        width: max-content;
        padding: 5px;
        position: absolute;
        border: 1px solid #bbbbbb;
        border-radius: 6px;
        overflow: auto;
        background-color: #fcfcfc;
        z-index: 99;
        max-height: 300px;
        min-height: 50px;
        transform: translateY((-10px));
        transition: 400ms ease-in-out;
    }

    .dropdown-placeholder {
        font-weight: 600;
        font-size:1.2em;
        font-family: NotoSansJP;
        color: #ffff88;
    }
    
